import React from 'react'
import './Widgets.css'
import SearchIcon from '@mui/icons-material/Search';
import {TwitterTimelineEmbed, TwitterShareButton, TwitterTweetEmbed} from "react-twitter-embed"

function Widgets() {
    return (
        <div className="widgets">
            <div className="widgets_searchDiv">
                <SearchIcon className="widgets_searchIcon"/>
                <input placeholder="Search Twitter" type="text"></input>
            </div>
            <div className="widgets_container">
                <h2>What's Happening</h2>
                <TwitterTweetEmbed tweetId = {'1426412775203508224'}/>

                <TwitterTimelineEmbed 
                sourceType = "profile"
                screenName = "pranavthedev"
                options = {{height: 400}}
                />

                <TwitterShareButton 
                url = {"https://www.instagram.com/anonymous_user.js/"}
                options = {{text: "Here goes nothing", via: "Anon"}}/>
            </div>
        </div>
    )
}

export default Widgets
