import React from "react";
import './Sidebar.css'
import SidebarOption from "./SidebarOption";
import TwitterIcon from '@mui/icons-material/Twitter';
import HomeOutlinedIcon from '@mui/icons-material/HomeOutlined';
import SearchRoundedIcon from '@mui/icons-material/SearchRounded';
import NotificationsNoneOutlinedIcon from '@mui/icons-material/NotificationsNoneOutlined';
import MailOutlineRoundedIcon from '@mui/icons-material/MailOutlineRounded';
import PersonOutlineRoundedIcon from '@mui/icons-material/PersonOutlineRounded';
import MoreHorizRoundedIcon from '@mui/icons-material/MoreHorizRounded';
import { Button } from "@mui/material";

function Sidebar(){
    return(
        <div className="sidebar">
            <TwitterIcon className="sidebar_twitterIcon"/>
            <SidebarOption active = {true} text="Home" Icon= {HomeOutlinedIcon}/>
            <SidebarOption text="Search" Icon= {SearchRoundedIcon}/>
            <SidebarOption text="Notification" Icon= {NotificationsNoneOutlinedIcon}/>
            <SidebarOption text="Messages" Icon= {MailOutlineRoundedIcon}/>
            <SidebarOption text="Profile" Icon= {PersonOutlineRoundedIcon}/>
            <SidebarOption text="More" Icon= {MoreHorizRoundedIcon}/>
            <Button variant="outlined" className="sidebar_tweet" fullWidth>Tweet</Button>
        </div>
    )
}

export default Sidebar